"use client";

import { Inter, Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import { ThemeProvider } from "@/components/theme-provider";
import { usePathname } from "next/navigation";
import Hedaer from "@/components/Hedaer";
import { ModeToggle } from "@/components/mode";
import { useState, useEffect, useMemo } from "react";
import { RightOutlined } from "@ant-design/icons";
import Cookies from "js-cookie";
import { User as UserIcon, Sparkles } from "lucide-react";
import Link from "next/link";

// --- TANSTACK QUERY IMPORTLARI ---
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

const inter = Inter({ subsets: ["latin"], variable: "--font-inter" });
const geistSans = Geist({ variable: "--font-geist-sans", subsets: ["latin"] });
const geistMono = Geist_Mono({ variable: "--font-mono", subsets: ["latin"] });

const SITE_CONFIG = {
  name: "CRM Edu Panel",
  description: "O'quv markazlari uchun professional boshqaruv tizimi",
  url: "https://8-preact-end.vercel.app/",
};

const cx = (...c: Array<string | false | undefined | null>) =>
  c.filter(Boolean).join(" ");

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();
  const hideHeader = pathname === "/login";

  const [isOpen, setIsOpen] = useState(true);
  const [user, setUser] = useState<any>(null);

  const [queryClient] = useState(
    () =>
      new QueryClient({
        defaultOptions: {
          queries: {
            staleTime: 60 * 1000,
            retry: 1,
          },
        },
      }),
  );

  const BASE_URL = process.env.NEXT_PUBLIC_BASE_URL || "http://localhost:7070";

  const pageTitle = useMemo(() => {
    const titles: { [key: string]: string } = {
      "/": "Boshqaruv Paneli",
      "/menagers": "Menejerlar",
      "/admins": "Adminlar",
      "/teachers": "O'qituvchilar",
      "/students": "Talabalar",
      "/groups": "Guruhlar",
      "/courses": "Kurslar",
      "/payment": "To'lovlar",
      "/settings": "Sozlamalar",
      "/profile": "Profil Ma'lumotlari",
    };

    // dynamic routes (masalan: /teachers/[id])
    if (pathname?.startsWith("/teachers/")) return "O'qituvchi Ma'lumoti";

    return titles[pathname] || "CRM Panel";
  }, [pathname]);

  useEffect(() => {
    if (typeof document !== "undefined") {
      document.title = `${pageTitle} | ${SITE_CONFIG.name}`;
    }
  }, [pageTitle]);

  useEffect(() => {
    const savedUser = Cookies.get("user") || localStorage.getItem("user");
    if (savedUser) {
      try {
        const parsed = JSON.parse(savedUser);
        setUser(parsed.data ? parsed.data : parsed);
      } catch {
        console.error("User parse error");
      }
    }
  }, [pathname]);

  const profileImg = useMemo(() => {
    if (!user?.image) return null;
    return user.image.startsWith("http") ? user.image : `${BASE_URL}/${user.image}`;
  }, [user, BASE_URL]);

  return (
    <html lang="uz" suppressHydrationWarning>
      <head>
        <meta name="description" content={SITE_CONFIG.description} />
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, maximum-scale=1"
        />
        <meta name="robots" content="noindex, nofollow" />
      </head>

      <body
        className={`${inter.variable} ${geistSans.variable} ${geistMono.variable} font-sans antialiased bg-background text-foreground`}
      >
        <QueryClientProvider client={queryClient}>
          <ThemeProvider
            attribute="class"
            defaultTheme="system"
            enableSystem
            disableTransitionOnChange
          >
            <div className="relative min-h-screen overflow-hidden">
              {/* BACKGROUND ACCENTS */}
              {!hideHeader && (
                <>
                  <div className="pointer-events-none absolute -top-40 left-1/3 h-[620px] w-[620px] rounded-full bg-primary/10 blur-[140px]" />
                  <div className="pointer-events-none absolute -bottom-48 -right-40 h-[620px] w-[620px] rounded-full bg-blue-500/10 blur-[160px]" />
                </>
              )}

              <div className="relative flex min-h-screen">
                {/* SIDEBAR */}
                {!hideHeader && (
                  <aside
                    className={cx(
                      "shrink-0 transition-[width] duration-300 ease-in-out",
                      "border-r border-border/60",
                      "bg-card/35 backdrop-blur supports-[backdrop-filter]:bg-card/25",
                      "shadow-[inset_-1px_0_0_hsl(var(--border))]",
                      isOpen ? "w-72" : "w-0 overflow-hidden border-none",
                    )}
                  >
                    <div className={cx("h-full", isOpen && "p-3")}>
                      {/* sidebar shell */}
                      <div className="h-full rounded-3xl border border-border/60 bg-background/30 backdrop-blur supports-[backdrop-filter]:bg-background/20 overflow-hidden">
                        <Hedaer isOpen={isOpen} />
                      </div>
                    </div>
                  </aside>
                )}

                {/* MAIN */}
                <div className="flex-1 flex flex-col min-w-0">
                  {/* TOPBAR */}
                  {!hideHeader && (
                    <header className="sticky top-0 z-40">
                      <div className="px-3 sm:px-4 pt-3">
                        <div
                          className={cx(
                            "flex items-center justify-between gap-3",
                            "rounded-3xl border border-border/60",
                            "bg-card/55 backdrop-blur supports-[backdrop-filter]:bg-card/40",
                            "shadow-sm",
                            "px-3 sm:px-4 py-2.5",
                          )}
                        >
                          {/* LEFT */}
                          <div className="flex items-center gap-3 min-w-0">
                            <button
                              onClick={() => setIsOpen((p) => !p)}
                              aria-label="Toggle Sidebar"
                              className={cx(
                                "h-10 w-10 rounded-2xl border border-border/60",
                                "bg-background/40 hover:bg-muted/40 transition",
                                "active:scale-95",
                                "grid place-items-center",
                              )}
                            >
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="22"
                                height="22"
                                viewBox="0 0 24 24"
                                fill="none"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                className="opacity-90"
                              >
                                <rect width="18" height="18" x="3" y="3" rx="2" />
                                <path d="M9 3v18" />
                              </svg>
                            </button>

                            {/* breadcrumb pill */}
                            <div className="min-w-0">
                              <div className="flex items-center gap-2 text-[11px] uppercase tracking-[0.22em] font-black text-muted-foreground">
                                <Sparkles size={14} className="text-primary" />
                                CRM
                              </div>

                              <nav className="flex items-center text-sm sm:text-base font-semibold min-w-0">
                                <span className="opacity-55 truncate">Asosiy</span>
                                <RightOutlined className="text-[10px] mx-2 opacity-30 shrink-0" />
                                <span className="truncate">{pageTitle}</span>
                              </nav>
                            </div>
                          </div>

                          {/* RIGHT */}
                          <div className="flex items-center gap-2 sm:gap-3">
                            <ModeToggle />

                            <Link
                              href="/profile"
                              className={cx(
                                "group flex items-center gap-2 sm:gap-3",
                                "rounded-full border border-border/60",
                                "bg-background/35 hover:bg-muted/30 transition",
                                "pl-2 pr-2 py-1",
                              )}
                            >
                              <div className="hidden sm:block text-right leading-tight">
                                <h3 className="text-sm font-bold truncate max-w-[140px]">
                                  {user?.first_name || "Menejer"}
                                </h3>
                                <p className="text-[10px] opacity-60 uppercase font-black tracking-widest">
                                  {user?.role || "Manager"}
                                </p>
                              </div>

                              <div className="h-10 w-10 relative rounded-full overflow-hidden border border-border/60 bg-muted flex items-center justify-center">
                                {profileImg ? (
                                  <img
                                    src={profileImg}
                                    alt="User avatar"
                                    className="w-full h-full object-cover"
                                  />
                                ) : (
                                  <UserIcon size={18} className="text-zinc-500" />
                                )}
                                <div className="pointer-events-none absolute inset-0 ring-1 ring-transparent group-hover:ring-primary/20 transition" />
                              </div>
                            </Link>
                          </div>
                        </div>
                      </div>
                    </header>
                  )}

                  {/* PAGE */}
                  <main
                    className={cx(
                      "flex-1 min-w-0",
                      hideHeader ? "p-0" : "p-4 md:p-6",
                      "overflow-y-auto",
                    )}
                  >
                    {children}
                  </main>
                </div>
              </div>
            </div>
          </ThemeProvider>
        </QueryClientProvider>
      </body>
    </html>
  );
}